.logo {
  display: grid;
  grid-template-columns: max-content 1fr;
  column-gap: 16px;

  width: max-content;
  color: inherit;

  @media (max-width: $max-mobile) {
    grid-template-columns: max-content;
  }

  &--footer {
    margin-bottom: 45px;

    @media (max-width: $max-tablet) {
      margin-bottom: 0;
      padding-left: 25px;
    }
  }
}

.logo img {
  grid-row: 1/3;

  width: 52px;
  height: 52px;

  @media (max-width: $max-mobile) {
    width: 38px;
    height: 38px;
  }
}

.logo__title {
  padding-top: 7px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;

  @media (max-width: $max-mobile) {
    display: none;
  }
}

.logo__tagline {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;

  color: var(--gray);

  @media (max-width: $max-mobile) {
    display: none;
  }
}
